<template>
  <div>
    <van-nav-bar title="我的订单" @click-left="Return" class="bgc">
      <van-icon name="arrow-left" slot="left" />
    </van-nav-bar>

    <div class="content">
      <van-tabs v-model="activeName" @change="onChange">
        <van-tab v-for="(act,ind) in actves" :key="ind" :title="act.title" :name="act.name">
          <div class="order-item" v-for="(item,index) in orderData" :key="index">
            <div class="order-top">
              <span>{{index}}</span>
              <van-icon
                v-if="item.status==2"
                name="delete"
                class="fr"
                @click="removeOrder(item,index)"
              />
            </div>

            <div class="order-cont">
              <van-card
                v-for="(v,i) in item.data"
                :key="i"
                :num="v.count"
                :price="v.price"
                :desc="v.desc"
                :title="v.name"
                :thumb="v.smallImg"
              >
                <template #tags>
                  <van-tag plain type="danger">标签</van-tag>
                  <van-tag plain type="danger">标签</van-tag>
                </template>
              </van-card>
            </div>
            <div class="btn clearfix">
              <span class="fl">{{item.data[0].createdAt|formatDate("yyyy-MM-dd hh:mm:ss")}}</span>
              <div class="is-receive fr">
                <span size="mini" v-if="item.status == 1" @click="Receive(item,index)">确认收货</span>
                <span v-else>已收货</span>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import "../../assets/css/commaint.less";
import "../../assets/css/myorder.less";
export default {
  data() {
    return {
      activeName: "0",

      // 订单编号
      oids: [],

      actves: [
        { title: "全部订单", name: 0 },
        { title: "待收货", name: 1 },
        { title: "已收货", name: 2 }
      ],

      // 商品数据
      orderData: {}
    };
  },
  created() {
    if (this.$route.params.status == 1 || this.$route.params.status == 2) {
      this.activeName = this.$route.params.status;
      this.getOrderData();
    } else {
      this.getOrderData();
    }
  },
  methods: {
    // 返回
    Return() {
      this.$router.push({ name: "My" });
    },

    // 获取订单数据
    getOrderData() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: this.activeName
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 70000) {
            this.$toast(res.data.msg);

            // 排序
            res.data.result.sort((a, b) => {
              return (
                new Date(b.createdAt).getTime - new Date(a.createdAt).getTime
              );
            });

            // 获取所有订单编号
            let oids = [];
            res.data.result.forEach(v => {
              // 排除重复订单号
              if (oids.indexOf(v.oid) === -1) {
                oids.push(v.oid);
              }
            });
            this.oids = oids;

            // 根据订单编号查找商品
            let orderData = {};
            res.data.result.forEach(v => {
              if (!orderData[v.oid]) {
                orderData[v.oid] = {
                  data: [v],
                  status: v.status
                };
              } else {
                orderData[v.oid].data.push(v);
              }
            });
            this.orderData = orderData;
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    onChange() {
      this.getOrderData();
    },

    // 确认收获
    Receive(item, index) {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/receive",
        data: {
          appkey: this.appkey,
          tokenString,
          oid: index
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 80000) {
            item.status = 2;
            this.$toast("已收货");
            this.getOrderData();
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 删除订单
    removeOrder(item, index) {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/removeOrder",
        data: {
          appkey: this.appkey,
          tokenString,
          oid: index
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 90000) {
            this.oids.splice(index, 1);
            this.getOrderData();
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
</style>